<template>
  <div
    class="intervals rounded-b-3xl p-6 bg-primary flex items-center justify-around md:justify-evenly capitalize text-primary-light lg:w-56 lg:flex-col lg:items-start">
    <div
      :class="{'text-white' : isActiveInterval(option)}"
      class="text-lg font-bold pt-5 hover:text-white cursor-pointer transition"
      @click="$store.commit('setInterval', option)"
      v-for="option in options"
      :key="option">
      {{option}}
    </div>
  </div>
</template>

<script>
  export default {
    name: 'intervals',
    data() {
      return {
        options: ['daily', 'weekly', 'monthly']
      }
    },
    methods: {
      isActiveInterval(option) {
        return option === this.$store.state.interval;
      }
    },

  }
</script>

