<template>
  <div class="container rounded-3xl p-6 bg-primary hover:bg-hover cursor-pointer transition text-white relative mb-16 md:mb-10 lg:w-56">
    <div class="flex items-center justify-between mb-1">
      <div class="text-lg font-bold">{{section.title}}</div>
      <img src="content/images/icon-ellipsis.svg"/>
    </div>

    <div class="flex items-center justify-between sm:flex-col sm:items-start">
      <div class="text-4xl sm:text-5xl sm:pt-3 pb-2">{{section.timeframes[$store.state.interval].current}}hrs</div>
      <div class="text-sm font-bold text-primary-light">{{intervalText()}} - {{section.timeframes[$store.state.interval].previous}}hrs</div>
    </div>

    <div class="absolute w-full h-16 rounded-t-3xl inset-0 -mt-10 section-bg bg-no-repeat" :class="'bg-'+section.theme" :style="`background-image:url('content/images/icon-${section.theme}.svg')`">
    </div>
  </div>
</template>

<script>
  export default {
    name: 'container',
    props: ['section'],
    computed: {
      activeInterval() {
        return this.$store.state.interval
      }
    },
    methods: {
      intervalText() {
        switch(this.$store.state.interval) {
          case 'daily':
            return 'Yesterday'
            break;
          case 'weekly':
            return 'Last Week'
            break;
          case 'monthly':
            return 'Last Month'
            break;
        }
      }
    },

  }
</script>

<style lang="scss">
.section-bg{
  background-position: right 20px center;
  z-index:-1;
  background: hsl(236, 100%, 87%);
  background: hsl(235, 45%, 61%)
}

</style>